                            texto: `
<p>Regulamenta o Decreto 3.048/99:<br/>
Art. 337.  O acidente do trabalho ser caracterizado tecnicamente pela Percia Mdica Federal, por meio da identificao do nexo causal entre o trabalho e o agravo.<br/>
 1 O setor de benefcios do Instituto Nacional do Seguro Social reconhecer o direito do segurado  habilitao do benefcio acidentrio.<br/>
 2 Ser considerado agravamento do acidente aquele sofrido pelo acidentado quanto estiver sob a responsabilidade da reabilitao profissional.<br/>
 3o  Considera-se estabelecido o nexo entre o trabalho e o agravo quando se verificar nexo tcnico epidemiolgico entre a atividade da empresa e a entidade mrbida motivadora da incapacidade, elencada na Classificao Internacional de Doenas - CID em conformidade com o disposto na Lista C do Anexo II deste Regulamento. (Redao dada pelo Decreto n 6.957, de 2009)<br/>
 4o  Para os fins deste artigo, considera-se agravo a leso, doena, transtorno de sade, distrbio, disfuno ou sndrome de evoluo aguda, subaguda ou crnica, de natureza clnica ou subclnica, inclusive morte, independentemente do tempo de latncia. (Includo pelo Decreto n 6.042, de 2007).<br/>
  5  Reconhecidos pela Percia Mdica Federal a incapacidade para o trabalho e o nexo causal entre o trabalho e o agravo, na forma prevista no  3, sero devidas as prestaes acidentrias a que o beneficirio tiver direito.    (Redao dada pelo Decreto n 10.410, de 2020)<br/>
 6  A Percia Mdica Federal deixar de aplicar o disposto no  3 quando demonstrada a inexistncia de nexo causal entre o trabalho e o agravo, sem prejuzo do disposto nos  7 e  12.   (Redao dada pelo Decreto n 10.410, de 2020)<br/>
 7o  A empresa poder requerer ao INSS a no aplicao do nexo tcnico epidemiolgico ao caso concreto mediante a demonstrao de inexistncia de correspondente nexo entre o trabalho e o agravo. (Redao dada pelo Decreto n 6.939, de 2009)<br/>
 8o  O requerimento de que trata o  7o poder ser apresentado no prazo de quinze dias da data para a entrega, na forma do inciso IV do art. 225, da GFIP que registre a movimentao do trabalhador, sob pena de no conhecimento da alegao em instncia administrativa. (Includo pelo Decreto n 6.042, de 2007).<br/>
 9  Caracterizada a impossibilidade de atendimento ao disposto no  8, motivada pelo no conhecimento tempestivo do diagnstico do agravo, o requerimento de que trata o  7 poder ser apresentado no prazo de quinze dias, contado da data em que a empresa tomar cincia da deciso a que se refere o  5.    (Redao dada pelo Decreto n 10.410, de 2020)<br/>
 10.  Juntamente com o requerimento de que tratam os  8o e 9o, a empresa formular as alegaes que entender necessrias e apresentar as provas que possuir demonstrando a inexistncia de nexo entre o trabalho e o agravo. (Redao dada pelo Decreto n 6.939, de 2009)<br/>
 11.  A documentao probatria poder trazer, entre outros meios de prova, evidncias  tcnicas circunstanciadas e tempestivas  exposio do segurado, podendo ser produzidas no mbito de programas de gesto de risco, a cargo da empresa, que possuam responsvel tcnico legalmente habilitado. (Includo pelo Decreto n 6.042, de 2007).<br/>
 12.  O INSS informar ao segurado sobre a contestao da empresa para que este, querendo, possa impugn-la, obedecendo, quanto  produo de provas, ao disposto no  10, sempre que a instruo do pedido evidenciar a possibilidade de reconhecimento de inexistncia do nexo entre o trabalho e o agravo. (Redao dada pelo Decreto n 6.939, de 2009)<br/>
 13.  Da deciso do requerimento de que trata o  7o cabe recurso, com efeito suspensivo, por parte da empresa ou, conforme o caso, do segurado ao Conselho de Recursos da Previdncia Social, nos termos dos arts. 305 a 310. (Includo pelo Decreto n 6.042, de 2007).</p>

<p>O Diploma Previdencirio regulamenta que a Percia Mdica Federal  PMF -  tem competncia para determinar se uma incapacidade decorre ou no do trabalho. Para isso, precisa relacionar o agravo (incapacidade laboral) com a Classificao Internacionais de Doenas dispostas no Anexo II, Lista C.<br/>
Tal procedimento foi realizado pela PMF e, por haver o enquadramento legal, foi concedida a espcie acidentria.<br/>
No h a necessidade de vistoria tcnica em razo do Decreto 3.048/99 ser taxativo quanto as doenas que tm origem no trabalho.</p>`,
