                            texto: '<p>DO ENQUADRAMENTO A AGENTE BIOLGICO</p><p>Normatiza o Decreto n 3.048/99 sobre o reconhecimento da especialidade em critrios qualitativos:<br>Art. 68. A relao dos agentes nocivos qumicos, fsicos, biolgicos ou associao de agentes prejudiciais  sade ou  integridade fsica, considerados para fins de concesso de aposentadoria especial, consta do Anexo IV. 1 &nbsp;A Secretaria Especial de Previdncia e Trabalho do Ministrio da Economia promover a elaborao de estudos com base em critrios tcnicos e cientficos para atualizao peridica do disposto no Anexo IV.<br> 2 &nbsp;A avaliao qualitativa de riscos e agentes prejudiciais  sade ser comprovada pela descrio: &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&nbsp;<br>I - das circunstncias de exposio ocupacional a determinado agente ou associao de agentes prejudiciais  sade presentes no ambiente de trabalho durante toda a jornada de trabalho; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;<br>II - de todas as fontes e possibilidades de liberao dos agentes mencionados no inciso I;<br>III - dos meios de contato ou exposio dos trabalhadores, as vias de absoro, a intensidade da exposio, a frequncia e a durao do contato.<br>()<br>ANEXO IV<br>CDIGO 3.0.1 - MICROORGANISMOS E PARASITAS INFECTO-CONTAGIOSOS VIVOS E SUAS TOXINAS<br>a) trabalhos em estabelecimentos de sade em contato com pacientes portadores de doenas infecto-contagiosas ou com manuseio de materiais contaminados;<br>b) trabalhos com animais infectados para tratamento ou para o preparo de soro, vacinas e outros produtos;<br>c) trabalhos em laboratrios de autpsia, de anatomia e antomo-histologia;<br>d) trabalho de exumao de corpos e manipulao de resduos de animais deteriorados;<br>e) trabalhos em galerias, fossas e tanques de esgoto;<br>f) esvaziamento de biodigestores;<br>g) coleta e industrializao do lixo.</p><p>Observando o texto regulamentador, verifica-se que algumas atividades desempenhadas, cdigo 3.0.1 podem ser caracterizadas com exposio a agentes biolgicos, desde que a descrio da atividade, circunstncias, possibilidades de liberao dos agentes, alm dos meios de contato ou exposio do trabalhador.</p>',
