                            texto: '<p>A controvrsia paira quanto ao reconhecimento de tempo especial para o intervalo a partir de 01.01.2004.</p><p><br>Conforme se verifica, o perodo no foi enquadramento em razo da metodologia utilizada para a apurao do rudo.</p><p><br>No presente caso, o(a) requerente apresentou Perfil Profissiogrfico Previdencirio para o intervalo de ________, laborado no cargo de _______, no setor de __________, da empresa _______________, ora indicada a exposio ao rudo acima de 85dBA.</p><p><br>De acordo com o Perfil Profissiogrfico Previdencirio, o rudo foi apurado com base na tcnica _______________.</p><p><br>Sobre esse tema, temos a Resoluo:</p><p>Resoluo n 73/2018:</p><p>APOSENTADORIA ESPECIAL. PEDIDO DE UNIFORMIZAO DE JUSRISPRUDNCIA. Divergncia jurisprudencial entre as Cmaras de Julgamento no que tange converso de tempo de atividade especial. Exigncia de histograma e/ou memria de clculo. Exposio ao agente nocivo rudo com a observncia da tcnica de apurao pela NHO-01 da Fundacentro. Competncia para anlise deste Conselho Pleno na forma do art. 3 inc. II do Regimento Interno do CRSS aprovado pela Portaria MDSA n 116/2017. Pressupostos de Admissibilidade do pedido alcanados na forma do art. 63 do mesmo Regimento. No  exigvel o fornecimento de histograma e/ou memria de clculo para o perodo de 11/10/2001 at 18/11/2003, por falta de previso legal em lei, decreto e ato normativo ministerial vinculado ao CRSS, bem como a inexigilibilidade dada ao inc. III do art. 280 da IN/INSS n 77/2015, pela DIRSAT/INSS, ante o reconhecimento da indisponibilidade de aparelhos de medio no perodo indicado. A exigncia do rudo em NEN, a partir de 18/11/2003, no  estritamente obrigatria podendo ser aceitas outras metodologias vlidas que atestem o rudo por meio de dosimetria. Precedente do Conselho Pleno. Pedido de Uniformizao conhecido e provido. Necessidade de reviso de ofcio do acrdo impugnado na forma do 12 do art. 63 do Regimento Interno do CRSS. (Grifos Nossos).<br>Atualmente, a matria est consolidada por meio do Enunciado n 13, do CRPS:</p><p>"ENUNCIADO N 13.<br>Atendidas as demais condies legais, considera-se especial, no mbito do RGPS, a atividade exercida com exposio a rudo superior a 80 decibis at 05/03/97, superior a 90 decibis desta data at 18/11/2003, e superior a 85 decibis a partir de ento.<br>I - Os nveis de rudo devem ser medidos, observado o disposto na Norma Regulamentadora n 15 (NR-15), anexos 1 e 2, com aparelho medidor de nvel de presso sonora, operando nos circuitos de compensao - dB (A) para rudo contnuo ou intermitente e dB (C) ou dB (linear) para rudo de impacto.<br>II - At 31 de dezembro de 2003, para a aferio de rudo contnuo ou intermitente,  obrigatria a utilizao das metodologias contidas na NR-15, devendo ser aceitos ou o nvel de presso sonora pontual ou a mdia de rudo, podendo ser informado decibelmetro, dosmetro ou medio pontual no campo "Tcnica Utilizada" do Perfil Profissiogrfico Previdencirio (PPP).<br>III - A partir de 1 de janeiro de 2004, para a aferio de rudo contnuo ou intermitente,  obrigatria a utilizao da tcnica/metodologia contida na Norma de Higiene Ocupacional 01 (NHO-01) da FUNDACENTRO ou na NR-15, que reflitam a medio de exposio durante toda a jornada de trabalho, vedada a medio pontual, devendo constar no PPP o nvel de rudo em Nvel de Exposio Normalizado - NEN ou a tcnica/metodologia "dosimetria" ou "udio dosimetria".<br>IV - Em caso de omisso ou dvida quanto  indicao da tcnica/metodologia utilizada para aferio da exposio nociva ao agente rudo, o PPP no deve ser admitido como prova da especialidade, devendo ser apresentado o respectivo Laudo Tcnico de Condies Ambientais do Trabalho (LTCAT) ou solicitada inspeo no ambiente de trabalho, para fins de verificar a tcnica utilizada na medio.</p><p>&nbsp;</p><p>1 hiptese:</p><p>No enquadramento</p><p><br>No se vislumbra a possibilidade de ser enquadrado o perodo a partir de 01.01.2004, pois embora o rudo possua intensidade acima de 85dBA, esse foi apurado com base em medio pontual e/ou decibelmetro, o que infringe a orientao prevista no inciso III, do Enunciado n 13, do CRPS.</p><p>&nbsp;</p><p>&nbsp;</p><p>2 Hiptese:</p><p>Enquadramento</p><p>H possibilidade do enquadramento do perodo a partir de 01.01.2004, eis que o rudo se encontra acima de 85dBA e a tcnica utilizada para a sua apurao foi a dosimetria da NR-15/Anexo 1, estando em conformidade com o inciso III, do Enunciado n 13, do CRPS.</p><p>&nbsp;</p>',
