                            texto: '<p>As razes recursais no contm assinatura; sendo notificada a parte requerente para regularizar, no sanou os vcios, nem justificou por que no o fez. Portanto, no caso dos autos, aplica-se o disposto no art. 76,  2, inciso I, do Cdigo de Processo Civil, c/c o art. 71 do Regimento Interno do CRPS, segundo o qual o recurso no ser conhecido quando a parte recorrente deixar de cumprir exigncia ou providncia que a ela incumbiria. Cdigo de Processo Civil Art. 76. Verificada a incapacidade processual ou a irregularidade da representao da parte, o juiz suspender o processo e designar prazo razovel para que seja sanado o vcio.  2 Descumprida a determinao em fase recursal perante tribunal de justia, tribunal regional federal ou tribunal superior, o relator: I - no conhecer do recurso, se a providncia couber ao recorrente; [...] Regimento Interno do Conselho de Recursos da Previdncia Social Art. 71. Nos casos de omisso deste Regimento, aplicam-se sucessivamente, se houver compatibilidade das regras, as disposies pertinentes da Lei n 13.105, de 16 de maro de 2015, que instituiu o Cdigo de Processo Civil, e da Lei no 9.784, de 29 de janeiro de 1999, que regula o processo administrativo no mbito da Administrao Pblica Federal. O STJ (Superior Tribunal de Justia) j manifestou pelo no conhecimento do mrito em recurso sem assinatura em que a parte recorrente negligencia e no regulariza aps ser notificada, consoante o entendimento jurisprudncia transcrito a seguir: PROCESSUAL CIVIL. ART. 13 e 284 DO CPC. AUSNCIA DE ASSINATURA NA PEA INICIAL, INSTNCIAS ORDINRIAS. ABERTURA DE PRAZO PARA REGULARIZAO. 1. A ausncia de assinatura na petio nas instncias ordinrias, ao contrrio da instncia especial,  um vcio sanvel, a teor do que reza o art. 13 do CPC, aplicvel analogicamente  irregularidade da representao postulatria, de forma que se deve proceder  abertura de prazo razovel para sanar a irregularidade.  que os vcios de representao devem ser sanados na instncia ordinria, pelo que, repise-se,  perfeitamente possvel ao Tribunal de origem a abertura de prazo para remediar esse tipo de defeito, consoante o disposto no referido dispositivo legal. 2. In casu, o juzo concedeu  autarquia oportunidade para firmar a inicial de embargos  execuo, transcorrendo o prazo de 40 (quarenta) dias sem qualquer atividade da parte. Deveras,  ausncia de assinatura da inicial aplica-se o art. 284 e seu pargrafo do CPC e, no o art. 267,  1, cujo escopo  diverso do primeiro dispositivo afastado. 3. Negligenciando a autarquia embargante  determinao do juzo a quo pra que procedesse  regularizao da petio inicial apcrifa, correta a extino dos embargos  execuo sem julgamento de mrito. 4. Recurso especial desprovido. (REsp 652.641/RS, Rel. Ministro LUIZ FUX, PRIMEIRA TURMA, julgado em 02/12/2004, DJ 28/02/2005, p. 236) Pelo exposto, VOTO no sentido de NO CONHECER DO RECURSO.</p>',
