                            texto: '<p>O recurso  tempestivo e esto presentes os demais pressupostos que admitem a anlise do mrito recursal.Quanto ao alegado labor rural de _____ &nbsp;a _____ &nbsp;, em que pese a qualificao civil de seu cnjuge, _____ , em momentos diversos (p. _____ ), no ficou comprovada a vinculao do grupo familiar a qualquer imvel rural, na forma de proprietrios, usufruturios, possuidores, assentados, parceiros, meeiros outorgados, comodatrios ou arrendatrios rurais, conforme exige o art. 11, VII, alnea a da Lei 8.213/91: Art. 11. So segurados obrigatrios da Previdncia Social as seguintes pessoas fsicas: (Redao dada pela Lei n 8.647, de 1993) [...] VII  como segurado especial: a pessoa fsica residente no imvel rural ou em aglomerado urbano ou rural prximo a ele que, individualmente ou em regime de economia familiar, ainda que com o auxlio eventual de terceiros, na condio de: (Redao dada pela Lei n 11.718, de 2008) a) produtor, seja proprietrio, usufruturio, possuidor, assentado, parceiro ou meeiro outorgados, comodatrio ou arrendatrio rurais, que explore atividade: (Includo pela Lei n 11.718, de 2008) Assim, no existe incio de prova suficiente para comprovar referida condio, na forma do Enunciado 8, V do CRPS, 2019, motivo pelo qual indefiro o processamento de justificao administrativa, na forma do art. 55, 3 da Lei 8.213/91: ENUNCIADO 8 O tempo de trabalho rural do segurado especial e do contribuinte individual, anterior  Lei n 8.213/91, pode ser utilizado, independente do recolhimento das contribuies, para fins de benefcios no RGPS, exceto para carncia. [...] &nbsp;V - O incio de prova material - documento contemporneo dotado de f pblica, sem rasuras ou retificaes recentes, constando a qualificao do segurado ou de membros do seu grupo familiar como rurcola, lavrador ou agricultor - dever ser corroborado por outros elementos, produzindo um conjunto probatrio harmnico, robusto e convincente, capaz de comprovar os fatos alegados. [...] &nbsp;(CONSELHO DE RECURSOS DA PREVIDNCIA SOCIAL, 2019) Art. 55. O tempo de servio ser comprovado na forma estabelecida no Regulamento, compreendendo, alm do correspondente s atividades de qualquer das categorias de segurados de que trata o art. 11 desta Lei, mesmo que anterior  perda da qualidade de segurado: [...]  3 A comprovao do tempo de servio para os fins desta Lei, inclusive mediante justificativa administrativa ou judicial, observado o disposto no art. 108 desta Lei, s produzir efeito quando for baseada em incio de prova material contempornea dos fatos, no admitida a prova exclusivamente testemunhal, exceto na ocorrncia de motivo de fora maior ou caso fortuito, na forma prevista no regulamento. (Redao dada pela Lei n 13.846, de 2019)</p>',
