                            categoria: 'voto-pontos-controversos',
                            texto: '<p>O recurso  tempestivo e esto presentes os demais pressupostos que admitem a anlise do mrito recursal.A recorrente busca o reconhecimento do exerccio de atividade rural, todavia ela no apresentou qualquer prova de atividade rural, tampouco a autodeclarao exigida, muito embora a comprovao da atividade se d complementarmente  autodeclarao, nos termos da Lei 8.213/91: Art. 106. A comprovao do exerccio de atividade rural ser feita, complementarmente  autodeclarao de que trata o  2 e ao cadastro de que trata o  1, ambos do art. 38-B desta Lei, por meio de, entre outros: (Redao dada pela Lei n 13.846, de 2019) Art. 38-B. [...]  2 Para o perodo anterior a 1 de janeiro de 2023, o segurado especial comprovar o tempo de exerccio da atividade rural por meio de autodeclarao ratificada por entidades pblicas credenciadas, nos termos do art. 13 da Lei n 12.188, de 11 de janeiro de 2010, e por outros rgos pblicos, na forma prevista no regulamento. (Includo pela Lei n 13.846, de 2019)Assim, no tendo ficado comprovada a condio de segurado especial, na forma do art.11, VII, alnea a da Lei 8.213/91, no entendo devido o benefcio de aposentadoria por idade rural, na forma do art. 26, III, 39, I c/c 48, 2 da Lei 8.213/91:<br>Lei 8.213/91<br>Art. 26. Independe de carncia a concesso das seguintes prestaes:<br>[...]<br>III - os benefcios concedidos na forma do inciso I do art. 39, aos segurados especiais referidos no inciso VII do art. 11 desta Lei;<br>[...]<br>Art. 39. &nbsp;Para os segurados especiais, referidos no inciso VII do caput do art. 11 desta Lei, fica garantida a concesso: &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(Redao dada pela Lei n 13.846, de 2019)<br>I - de aposentadoria por idade ou por invalidez, de auxlio-doena, de auxlio-recluso ou de penso, no valor de 1 (um) salrio mnimo, e de auxlio-acidente, conforme disposto no art. 86 desta Lei, desde que comprovem o exerccio de atividade rural, ainda que de forma descontnua, no perodo imediatamente anterior ao requerimento do benefcio, igual ao nmero de meses correspondentes  carncia do benefcio requerido, observado o disposto nos arts. 38-A e 38-B desta Lei; ou &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; (Redao dada pela Lei n 13.846, de 2019)<br>[...]<br>Art. 48 [...] &nbsp; &nbsp; &nbsp; &nbsp; 2o &nbsp;Para os efeitos do disposto no  1o deste artigo, o trabalhador rural deve comprovar o efetivo exerccio de atividade rural, ainda que de forma descontnua, no perodo imediatamente anterior ao requerimento do benefcio, por tempo igual ao nmero de meses de contribuio correspondente  carncia do benefcio pretendido, computado o perodo &nbsp;a que se referem os incisos III a VIII do  9o do art. 11 desta Lei. &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;(Redao dada pela Lei n 11,718, de 2008)<br>Pelo exposto, conheo do recurso e voto no sentido de NEGAR-LHE PROVIMENTO.&nbsp;<br>RECURSO ORDINRIO. APOSENTADORIA POR IDADE RURAL. PRETENDE A PARTE INTERESSADA O RECONHECIMENTO DE SUA CONDIO DE SEGURADO(A) ESPECIAL. REFERIDA NORMA EXIGE INCIO DE PROVA MATERIAL CONTEMPORNEO  POCA DOS FATOS A PROVAR, E PERMITE UTILIZAO, PARA ESSE FIM, DE DOCUMENTO ANTERIOR AO PERODO CONTROVERTIDO. NO COMPROVADA A CONDIO DE SEGURADO(a) ESPECIAL, NA FORMA DO ART.11, VII, ALNEA A DA LEI 8.213/91. NO H DIREITO AO BENEFCIO, NA FORMA DO ART. 26, III, 39, I C/C 48, 2 DA LEI 8.213/91. RECURSO CONHECIDO A QUE SE NEGA PROVIMENTO.<br>&nbsp;</p>',
